<template>
	<div class="container" id="container">
		<canvas id="sharePic" width="500" height="1000"></canvas>
	</div>
</template>

<script>
	export default {
		name: 'QRCode',
		data() {
			return {
				sh: '',
				sw: '',
				title: "鞋服专送080901期",
				tips1: "扫一扫或长按小程序识别码(二维码)",
				tips2: "即可直达集货页面",
				tips3: "长按识别二维码，查看详情",
				btnword: "保存分享小程序码",
				info: "全国流向 1.5-15kg | 每日承诺寄件量最低20件",
				imgSrc: "",
			}
		},
		computed: {
			teamid() {
				return this.$route.query.teamid;
			},
		},
		methods: {
			getData(){
				this.$axios.get('/qrcode/generate/'+this.teamid, {
				
				})
				.then((response) =>{
					if(response.data.success && response.data.data){
						let datakey = response.data.data;
						
						this.imgSrc = datakey.qrCodeImg;
						this.draw();
					}
				});
			},
			draw(){
				var ctx = document.getElementById("sharePic").getContext("2d");  
	            ctx.textAlign="center";
	            ctx.font = "bold 20px 微软雅黑";
	            ctx.fillStyle = "#FF6532"; 
	            ctx.textAlign="center";
	            ctx.fillText(this.title,250,58);
	            
	            ctx.textAlign="center";
	            ctx.font = "18px 微软雅黑";
	            ctx.fillStyle = "#353535";
	            ctx.fillText(this.info,250,108);
	            
	            var img=new Image()
				img.src="/static/Combined-Shape.png"
				img.onload = function () {
		            ctx.drawImage(img,25,50,450,2);
            	}
	            var img1=new Image()
				img1.src=this.imgSrc;
				img1.onload = function () {
		            ctx.drawImage(img1,125,160,250,250);
            	}
	            
	            ctx.font = "18px 微软雅黑";  
	            ctx.fillStyle = "#B2B2B2";  
	            ctx.fillText(this.tips1,250,480);
	            
	            ctx.font = "18px 微软雅黑";  
	            ctx.fillStyle = "#B2B2B2";  
	            ctx.fillText(this.tips2,250,510);

				ctx.fillStyle="#FF6532";
				this.drawRoundRect(ctx, 40, 550, 420, 60, 8); 
				ctx.fill();
				
				ctx.font = "22px 微软雅黑";  
	            ctx.fillStyle = "#FFFFFF";  
	            ctx.fillText(this.btnword,250,585);
	            
	            ctx.font = "16px 微软雅黑";  
	            ctx.fillStyle = "#B2B2B2";  
	            ctx.fillText(this.tips3,250,660);
			},
			drawRoundRect(cxt, x, y, width, height, radius){   
		        cxt.beginPath();   
		        cxt.arc(x + radius, y + radius, radius, Math.PI, Math.PI * 3 / 2);   
		        cxt.lineTo(width - radius + x, y);   
		        cxt.arc(width - radius + x, radius + y, radius, Math.PI * 3 / 2, Math.PI * 2);   
		        cxt.lineTo(width + x, height + y - radius);   
		        cxt.arc(width - radius + x, height - radius + y, radius, 0, Math.PI * 1 / 2);   
		        cxt.lineTo(radius + x, height +y);   
		        cxt.arc(radius + x, height - radius + y, radius, Math.PI * 1 / 2, Math.PI);   
		        cxt.closePath();
			}
		},
		mounted() {
			var box = document.getElementById("container")
			this.sh = parseInt(window.getComputedStyle(box).height)
			this.sw = parseInt(window.getComputedStyle(box).width)
			this.getData();
			
		}
	}
</script>
<style scoped>
canvas{
	width: 100%;
}
</style>


