<template>
	<div class="container">
		<v-group title="全部集货团">
			<v-cell v-for="item in pandectData" :title="item.teamName" :link="{path:'/teamDetail',query:{id:item.teamId}}">
				<v-tag :text="['','进行中','已成团','已结束'][item.status]" v-model="value"></v-tag>
			</v-cell>
		</v-group>
	</div>
</template>

<script>
	export default {
		name: 'teamList',
		data() {
			return {
				pandectData: [],
				value:''
			}
		},
		mounted() {
			this.getData();
			
		},
		methods:{
			getData(){
				this.$axios.post('/teamInfo/list', {
					
				})
				.then((response) =>{
					if(response.data.success && response.data.data){
						let datakey = response.data.data;
						
						this.pandectData = datakey;
					}
				});
			},
		}
	}
</script>

<style scoped lang="less">
				
</style>


