<template>
	<div class="container">
		<v-cell title="寄件地址" class="form-title">
			<img slot="icon" width="15" class="title-icon" src="../assets/icon-addr.png">
		</v-cell>
		
		<v-address 
         	@on-hide="" 
         	@on-show=""
         	@on-shadow-change="" 
         	title="所在地区" 
         	v-model="formData.addr" 
         	:list="addressData" 
         	:hide-district="false"
         	placeholder="请选择地区" 
         	inline-desc="" 
         	:show.sync="showAddress"
         	:hide-on-blur="false"
         	raw-value>
		</v-address>
      	<v-textarea v-model="formData.text" :max="200" name="detail" :placeholder="'请填写详细信息，不少于5个字'" :show-counter="false"></v-textarea>
		
		<div class="plain-margin"></div>

		<div class="plain-margin"></div>
		
		<v-cell title="寄件人" class="form-title begin-item">
			<img slot="icon" width="15" class="title-icon" src="../assets/icon-people.png">
		</v-cell>
		<v-input title="" placeholder="请填写寄件人姓名" v-model="formData.name"></v-input>
		<v-input title="" placeholder="请填写寄件人电话" v-model="formData.phone"></v-input>
		
		<div class="plain-margin"></div>
		
		<v-input title="预估每日寄件量" placeholder="(请如实填报，以便于我们为您准备资源)" class="begin-item"></v-input>
		<v-input title="" placeholder="请填写寄件数量（至少20件）" v-model="formData.num"></v-input>
		<v-input title="" placeholder="请选择单件平均重量" v-model="formData.average" max-length="5"></v-input>
		
		<div class="btn" @click="handleClick">
			报名集货
		</div>
		
		<v-dialog v-model="showToast" class="dialog-demo">
			<img class="correct" src="../assets/icon-correct.png" />
			
			<div class="title-1">报名成功！</div>
			<div class="title-2">转发给更多朋友或生成图片分享至朋友圈，即可加快集货进度~</div>
			
			<v-button class="btn-1" @click.native="toQR">发送微信好友</v-button>
			<v-button class="btn-2" @click.native="toQR">分享到朋友圈</v-button>
			
			<div @click="showToast=false">
				<span class="vux-close"></span>
			</div>
		</v-dialog>
	</div>
</template>

<script>
	export default {
		name: 'applyForm',
		data() {
			return {
				inlineDescText:"必填项（限定200个字符）",
				title: '默认为空',
				value: [],
				value2:'',
				addressData: [],
				showAddress: false,
      			showToast: false,
      			formData: {
      				addr:[],
      				text:'',
      				name:'',
      				phone:'',
      				num:'',
      				average:''
      			}
			}
		},
		computed: {
			teamid() {
				return this.$route.query.teamid;
			},
		},
		mounted() {
			this.getData();
		},
		methods: {
			toQR() {
				this.$router.push({path: '/QRCode', query:{teamid: this.teamid}});
			},
			getData(){
				this.$axios.post('/address/get', {
				
				})
				.then((response) =>{
					if(response.data.success && response.data.data){
						let datakey = response.data.data;
						
						this.addressData = datakey;
					}
				});
			},
			handleClick() {
				//验证详细信息
				if(this.formData.text.length<5) {
					this.$toast.show({
						text: '详细信息，不少于5个字！',
						type:'text',
						time: 1000,
						width:"auto"
					});
					return;
				}
				
				//验证手机号
		       	let mobilenumber = this.formData.phone;
				var myreg = /^[1][3,4,5,7,8][0-9]{9}$/; 
				
				if(!myreg.test(mobilenumber)) {
					this.$toast.show({
						text: '手机号格式不正确！',
						type:'text',
						time: 1000,
						width:"auto"
					});
					return;
				}
				
				//验证寄件数量
				let number1 = this.formData.num;
				if(!parseInt(number1) || parseInt(number1)<20) {
					this.$toast.show({
						text: '寄件数量至少20件！',
						type:'text',
						time: 1000,
						width:"auto"
					});
					return;
				}

				//提交报名信息
				let param = {
					sendNum: this.formData.num,  //寄件量
				    sendPh: this.formData.phone, // 寄件人电话
				    sendUser: this.formData.name, //寄件人姓名
				    sendWeight: this.formData.average,// 平均重量
				    teamId: this.teamid,   //团ID
				    userAdd: this.formData.text,//详细地址
				    userBq: this.formData.addr.join(','), //所在地区
				}
				this.$axios.post('/teamUser/add', 
				 	param
				)
				.then((response) =>{
					if(response.data.success && response.data.data){
						let datakey = response.data.data;
						
						this.showToast = true;
					}
				});
				
			}
		},
	}
</script>

<style scoped lang="less">
	@main-color: #FF6532;
	.form-title {
		color: @main-color;
		font-size: 16px;
		
		/*&.address {
			background-image: url(../assets/icon-addr.png);
		    background-repeat: no-repeat;
			background-size: contain;
		}*/
	}
	.plain-margin {
		background: #F8F8F8;
		height: 6px;
	}
	.last-item {
		margin-bottom: 10px;
	}
	.begin-item::before {
		display: none;
	}
	.title-icon {
		margin: 5px 10px 0 0;
	}
	.correct {
		width: 80px;
		height: auto;
		margin: 20px 0;
	}
	
	.title-1 {
	   font-size: 22px;
	   line-height: 30px;
	}
	.title-2 {
	   font-size: 14px;
	   margin: 10px 0;
	}
	
	.btn-1 {
		background: #09bb07;
		color: #fff;
	}
	.btn-2 {
	   font-size: 14px;
	   margin: 10px 0;
	}
</style>


